.header {
  &__section {
    position: fixed;
    z-index: 30;
    top: 0;
    width: 100%;
    height: 60px;
  }
  &__container {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 6px;
  }

  &__logo {
    @extend %link;
    color: var(--accent-color);

    font-family: var(--accent-font);
    font-weight: 400;
    font-size: 26px;
    letter-spacing: 0.04em;
    line-height: 1.15;
    text-transform: uppercase;

    @media screen and ($mobile) {
      // margin-left: 20px;
      // margin-top: 6px;
    }

    @media screen and ($tablet) and (max-width: 1279px) {
      // margin-left: 20px;
      // margin-top: 52px;
    }
  }

  // &__logo--top {
  //   // display: block;
  //   border-radius: 50%;
  //   width: 50px;
  //   height: 50px;
  //   margin-right: 20px;
  //   padding-top: 10px;
  //   padding-left: 10px;
  //   background-color: var(--accent-color);
  // }

  &__logo--icon {
    fill: var(--secondary-bg-color);
  }

  &__nav--list {
    @extend %list;
    // align-items: center;
    font-family: var(--main-font);
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 0.04em;
    line-height: 1.31;
    color: var(--inverted-text-color);
    @media screen and (max-width: 1279px) {
      display: none;
    }

    @media screen and ($desktop) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__nav--link {
    @extend %link;
  }
}

.main-nav {
  display: flex;
  justify-content: space-between;
}

.header__background {
  position: fixed;
  z-index: 10;
  top: 0;
  width: 100%;
  height: 60px;
  background-color: #ffa5ba80;
}

.order-btn {
  @include button(normal, 200px);
  margin: 0 auto;

  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    top: 52px;
    right: 34px;
  }

  &__icon {
    margin-left: 16px;
    width: 10px;
    height: 10px;
  }
}

.icon-angle {
  fill: currentColor;
}

.menu__btn-burger {
  width: 40px;
  height: 40px;
  
  margin-left: 100px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  @media screen and ($desktop) {
    display: none;
  }

  .menu__icon {
    width: 20px;
    height: 12px;
    fill: var(--accent-color);
  }
}
